import { Component, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, OTHER_STATUS, STATUSES } from '@shared';
import { Product } from '@models';
import Swal from 'sweetalert2';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss']
})
export class ProductComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent
  dataSource: MatTableDataSource<Product>;
  displayedColumns: string[] = ['brand', 'productDescription', 'category', 'status'];
  statusList = [...STATUSES];

  product = new Product();
  productList = [];
  brandList = [];
  categoryList = [];

  isFromModal = false;
  showApproveRejectButton = false;
  isShowApproveDisapproveButtonByAdmin = false;

  userRole = '';

  productForm: FormGroup;
  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dataStorage: DataStorageService,
    @Optional() public dialogRef: MatDialogRef<ProductComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    this.userRole = this.dataStorage.getUserRole();
    if (data) {
      if (data.dataForModal) {
        const dataFromModal = data.dataForModal;

        console.log(dataFromModal);

        this.isFromModal = dataFromModal.isFromModal;
        this.showApproveRejectButton = dataFromModal.isUpdate;

        if (dataFromModal.isUpdate) {
          this.product = dataFromModal.product;
        } else {
          this.product.brand = dataFromModal.brand;
        }
      }
    }
  }

  ngOnInit() {
    if (!this.isFromModal) {
      this.getList();
    }
    this.setFormValidator();
    this.getDropdownList();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.product).subscribe((res: any) => {
      this.productList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Product>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  getDropdownList() {
    this.apiService.findAll(ENDPOINTS.activeBrands).subscribe((res: any) => {
      this.brandList = res.responseData.data;
    });

    this.apiService.findAll(ENDPOINTS.category).subscribe((res: any) => {
      this.categoryList = res.responseData.data;
      this.categoryList = res.responseData.data.filter(
        (category: any) => category.status === COMMON_FUNCTION.ACTIVE);
    });
  }

  save(): void {
    if (this.isFromModal) {
      this.product.status = OTHER_STATUS.FOR_APPROVAL;
    }
    this.apiService.save(ENDPOINTS.product, this.product).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
          this.dataStorage.setS1NewProduct(res.responseData.data);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        if (this.isFromModal) {
          this.dialogRef.close(this.dataStorage.getS1NewProduct());
        } else {
          this.resetFormValidator();
          this.getList();
        }
      });
  }

  getProduct(): void {
    this.apiService.findById(ENDPOINTS.product, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.product = res.responseData.data;

        this.isShowApproveDisapproveButtonByAdmin = this.product.status === 'FOR_APPROVAL' ?
          true : false;
      }
    });
  }

  update(): void {
    if (this.data?.dataForModal?.currentStatus === OTHER_STATUS.FOR_APPROVAL && this.product.status !== COMMON_FUNCTION.APPROVED) {
      this.product.status = OTHER_STATUS.FOR_APPROVAL;
    } else if (this.product.status === COMMON_FUNCTION.APPROVED) {
      this.product.status = COMMON_FUNCTION.ACTIVE;
    }

    this.apiService.update(ENDPOINTS.product, this.product).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
          if (this.product.status !== COMMON_FUNCTION.DISAPPROVED.toUpperCase() &&
            this.product.status !== COMMON_FUNCTION.INACTIVE.toUpperCase()) {
            this.dataStorage.setS1NewProduct(res.responseData.data);
          } else {
            this.dataStorage.setS1NewProduct(new Product());
          }
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        if (this.isFromModal) {
          this.dialogRef.close(this.dataStorage.getS1NewProduct());
        } else {
          this.resetFormValidator();
          this.getList();
        }
      });
  }

  setFormValidator(): void {
    this.product.status = COMMON_FUNCTION.ACTIVE;
    this.productForm = this.formBuilder.group({
      description: ['', Validators.required],
      status: [''],
      brand: ['', Validators.required],
      category: ['', Validators.required]
    });
  }

  resetFormValidator(): void {
    this.product = new Product();
    this.isShowApproveDisapproveButtonByAdmin = false;
    this.product.status = COMMON_FUNCTION.ACTIVE;
    this.productForm.markAsUntouched();
    this.productForm.markAsPristine();
  }

  compareCategory(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareBrand(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  delete() {

    Swal.fire({
      text: 'Are you sure you want to delete this product?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.product, this.product.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Product successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

  approve() {
    this.product.status = 'APPROVED';
    this.update();
  }

  reject() {
    this.product.status = 'DISAPPROVED';
    this.update();
  }

}
